import React from 'react';

function BankInfo(props) {
    const bankInfo = props.bankInfo;
    return (
        <>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Thng tin ngn hng {bankInfo.name}</h4>
                        </div>
                        <div className='card-body'>
                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Ngn Hng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.name}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Tnh trng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.status === 1 ? (
                                        <span className="badge badge-success">Hot ng</span>
                                    ) : (
                                        <span className="badge badge-danger">ang Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>ang S Dng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.user_status === 1 ? (
                                        <span className="badge badge-success">Bt</span>
                                    ) : (
                                        <span className="badge badge-danger">Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi m ngn hng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.opening_fee}  (Vnh Vin)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi m ti khon:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.account_opening_fee}  (t ti khon th 2)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>M ti a:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.max_accounts} ti khon
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Gi np tin vo TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_deposit_fee}  + {bankInfo.app_deposit_fee_percentage}% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className='text-info'>Gi chuyn tin trong TK APP:</strong>
                                </div>
                                <div className="col text-info">
                                    {bankInfo.app_transfer_fee}  + {bankInfo.app_transfer_fee_percentage}% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong>Gi kim tra s ti khon t ng:</strong>
                                </div>
                                <div className="col">
                                    {bankInfo.auto_check_account_fee}  / lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col">
                                    <strong className="text-warning">Lu  quan trng:</strong>
                                </div>
                                <div className="col text-warning">
                                    {bankInfo.important_note}
                                </div>
                            </div>
                            <div className='p-3'>
                                <div
                                    className="toolbar toolbar-bottom"
                                    role="toolbar"
                                    style={{ textAlign: "right" }}
                                >
                                    <button className="btn btn-danger sw-btn-next btn-sm" type="button">
                                        ng Ngn Hng
                                    </button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Danh sch ti khon</h4>
                        </div>
                        <div className='card-body'>
                            <div className="table-responsive">
                                <div id="example_wrapper" className="dataTables_wrapper">
                                    <div className='row'>
                                        <div className='col-6'>
                                            <div id="example_filter" className="dataTables_filter">
                                                <label>
                                                    Tm Kim :
                                                    <input type="search" className="" placeholder="" aria-controls="example" />
                                                </label>
                                            </div>
                                        </div>
                                        <div className='col-6'>
                                            <div
                                                className="toolbar toolbar-bottom"
                                                role="toolbar"
                                                style={{ textAlign: "right" }}
                                            >
                                                <button className="btn btn-primary sw-btn-next btn-sm" type="button">
                                                    Thm Ti Khon
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                    <table
                                        id="example"
                                        className="display dataTable"
                                        style={{ minWidth: 845 }}
                                        role="grid"
                                        aria-describedby="example_info"
                                    >
                                        <thead>
                                            <tr role="row">
                                                <th
                                                    className="sorting_asc"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-sort="ascending"
                                                    aria-label="Name: activate to sort column descending"
                                                    style={{ width: "263.5px" }}
                                                >
                                                    Tn
                                                </th>
                                                <th
                                                    className="sorting"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-label="Office: activate to sort column ascending"
                                                    style={{ width: "203.531px" }}
                                                >
                                                    a ch
                                                </th>
                                                <th
                                                    className="sorting"
                                                    tabIndex={0}
                                                    aria-controls="example"
                                                    rowSpan={1}
                                                    colSpan={1}
                                                    aria-label="Age: activate to sort column ascending"
                                                    style={{ width: "85.6562px" }}
                                                >
                                                    Tui
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr className="odd" role="row">
                                                <td className="sorting_1">Airi Satou</td>
                                                <td>Tokyo</td>
                                                <td>33</td>
                                            </tr>
                                            <tr className="even" role="row">
                                                <td className="sorting_1">Angelica Ramos</td>
                                                <td>London</td>
                                                <td>47</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default BankInfo;